

































import Vue from 'vue';
import { User, ProfileState } from '@/store/modules/profile/types';

export default Vue.extend({
  name: 'UserInfo',
  data() {
    return {
      options: [
        {
          value: 'szl',
          label: '神之路',
        },
        {
          value: 'sx',
          label: '思仙',
        },
        {
          value: 'fzxx',
          label: '放置修仙',
        },
      ],
    };
  },
  computed: {
    game() {
      const state: ProfileState = this.$store.state.profile;
      if (state.user) {
        return state.user!.sGameFlag;
      }
      return '';
    },
  },
});
